import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import DatePicker from "react-datepicker";
import RadioButton from "../../../../common/RadioButton/RadioButton";

import * as selectors from "../../../../store/selectors";

import "react-datepicker/dist/react-datepicker.css";
import styles from './step3.module.sass'
import { getRate } from "../../../../store/actions";
import Checkbox from "../../../../common/Checkbox/Checkbox";

const checkboxContent = [
    {
        id: 1,
        title: ' , 500'
    },
    {
        id: 2,
        title: ' , 200'
    },
    {
        id: 3,
        title: ' , 1600'
    }

]

const Step3 = () => {
    const dispatch = useDispatch()
    const car = useSelector(selectors.car)
    const cars = useSelector(selectors.cars)
    const rate = useSelector(selectors.rate)
    const selectedCar = cars.find((item) => car === item.id).colors
    const [startDate, setStartDate] = useState();
    const [endDate, setEndDate] = useState();
    const [checkStatus, setCheckStatus] = useState(false);
    console.log((endDate - startDate) / (1000 * 60) * 7)
    const onCheck = () => setCheckStatus(!checkStatus)

    useEffect(() => {
        if (!rate.length)
            dispatch(getRate())

    }, [dispatch])
    return (
        <div>
            <div>
                <div className={styles.label}></div>
                <div className={styles.colorSelect}>
                    {selectedCar.map((item) => {
                        return (
                            <RadioButton
                                key={item}

                            >{item}</RadioButton>
                        )
                    })}
                </div>

            </div>
            <div>
                <div className={styles.label}> </div>
                <div className={styles.date}>
                    <div className={styles.titleDatePicker}></div>
                    <DatePicker
                        placeholderText="   "
                        dateFormat="dd.MM.yyyy HH:mm"
                        timeFormat="HH:mm"
                        isClearable
                        selectsStart
                        showTimeSelect
                        className={styles.datePicker}
                        startDate={startDate}
                        endDate={endDate}
                        selected={startDate}
                        minDate={new Date()}
                        onChange={(date) => setStartDate(date)}
                    />
                </div>
                <div className={styles.date}>
                    <div className={styles.titleDatePicker}></div>
                    <DatePicker
                        placeholderText="   "
                        dateFormat="dd.MM.yyyy HH:mm"
                        timeFormat="HH:mm"
                        isClearable
                        selectsEnd
                        showTimeSelect
                        className={styles.datePicker}
                        minDate={startDate}
                        startDate={startDate}
                        endDate={endDate}
                        selected={endDate}
                        onChange={(date) => setEndDate(date)}
                    />
                </div>
            </div>
            <div>
                <div className={styles.label}></div>
                {rate.map((item) => {
                    return (
                        <RadioButton
                            key={item.id}
                        >
                            {item.rateTypeId.name},{item.price}/ {item.rateTypeId.unit}
                        </RadioButton>
                    )
                })}
            </div>
            <div>
                <div className={styles.label}> </div>
                <div className={styles.additionalSelect}>
                    {checkboxContent.map((item) => {
                        return (
                            <Checkbox
                                key={item.id}>
                                {item.title}
                            </Checkbox>
                        )
                    })}
                </div>
            </div>
        </div>
    )
}
export default Step3